* {
  font-family: Arial, Helvetica, sans-serif;
}

body {
  padding: 3rem;
}

input {
  font-size: medium;
  width: 50%;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card label {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 170px;
}
